import React, { useState } from 'react';
import axios from 'axios';

const Sample = () => {
    const [userQuery, setUserQuery] = useState('');
    const [flightInfo, setFlightInfo] = useState(null);
    const [error, setError] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setFlightInfo(null);

        try {
            // Send the user query to the backend
            const response = await axios.post('/chatbot', userQuery, {
                headers: {
                    'Content-Type': 'text/plain', // Set the content type to plain text
                },
            });
            setFlightInfo(response.data); // Set flight information from response
        } catch (err) {
            setError('Error fetching data from the server.');
            console.error(err);
        }
    };

    return (
        <div>
            <h1>Chatbot</h1>
            <form onSubmit={handleSubmit}>
                <textarea
                    value={userQuery}
                    onChange={(e) => setUserQuery(e.target.value)}
                    placeholder="Ask me about flights..."
                    required
                />
                <button type="submit">Send</button>
            </form>

            {error && <p style={{ color: 'red' }}>{error}</p>}

            {flightInfo && (
                <div>
                    <h2>Flight Information:</h2>
                    <p>Origin: {flightInfo.origin}</p>
                    <p>Destination: {flightInfo.destination}</p>
                    <p>Date: {flightInfo.date}</p>
                </div>
            )}
        </div>
    );
};
export default Sample;
